import React, { useState, useEffect, useCallback } from "react";
import {
  HStack,
  Text,
  FlatList,
  Pressable,
  Center,
  VStack,
  CheckCircleIcon,
  Popover,
  Box,
} from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";
import {
  useStripe,
  useElements,
  CardElement,
  Elements,
  CardNumberElement,
  CardExpiryElement,
  CardCvcElement,
} from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";

import { vw, vh } from "../../../util";
import { STRIPE_PK } from "../../../constants/Config";
import ShadowModal from "../../../components/ShadowModal";
import ShadowInput from "../../../components/ShadowInput";
import ShadowButton from "../../../components/ShadowButton";
import * as planActions from "../../../store/actions/plan";
import Colors from "../../../constants/Colors";
import MasterCardICon from "../../../components/svg/MasterCardIcon";
import VisaCardIcon from "../../../components/svg/VisaCardIcon";
import CardRemoveIcon from "../../../components/svg/CardRemoveIcon";

import * as authActions from "../../../store/actions/auth";
const stripePromise = loadStripe(STRIPE_PK);

const StripeWrapper = ({ children }) => {
  return <Elements stripe={stripePromise}>{children}</Elements>;
};

const CardForm = ({
  _showAlert,
  profile,
  setIsOpen,
  dispatch,
  handleSubmit,
  setRefresh,
  reset,
}) => {
  const elements = useElements();
  const stripe = useStripe();
  const [isSaving, setIsSaving] = useState(false);
  const [card, setCard] = useState(null);

  const onChange = (event) => {
    const n = elements.getElement(CardNumberElement);
    // const n_isInvalid = n._invalid;
    // const n_isEmpty = n._empty;
    // const n_isValid = !(n_isEmpty || n_isInvalid);
    // if (!n_isValid) {
    //   _showAlert("error", "Missing or invalid card number.");
    //   return false;
    // } else {
    //   const e = elements.getElement(CardExpiryElement);
    //   e.focus();
    // }
    setCard(n);
  };

  const onSubmit = async (data) => {
    try {
      setIsSaving(true);
      if (profile && !profile.address) {
        const action = authActions.updateProfile({
          _id: profile._id,
          address: data,
        });
        await dispatch(action);
      }
      await createPM(data);
    } catch (err) {
      _showAlert("error", err.message);
      setIsSaving(false);
    }
  };

  const checkCardIsValid = () => {
    const n = elements.getElement(CardNumberElement);
    const e = elements.getElement(CardExpiryElement);
    const c = elements.getElement(CardCvcElement);

    const n_isInvalid = n._invalid;
    const n_isEmpty = n._empty;
    const n_isValid = !(n_isEmpty || n_isInvalid);
    if (!n_isValid) {
      _showAlert("error", "Missing or invalid card number.");
      return false;
    }

    const e_isInvalid = e._invalid;
    const e_isEmpty = e._empty;
    const e_isValid = !(e_isEmpty || e_isInvalid);
    if (!e_isValid) {
      _showAlert("error", "Missing or invalid expiry date.");
      e.focus();
      return false;
    }

    const c_isInvalid = c._invalid;
    const c_isEmpty = c._empty;
    const c_isValid = !(c_isEmpty || c_isInvalid);
    if (!c_isValid) {
      c.focus();
      _showAlert("error", "Missing or invalid CVC.");
      return false;
    }

    return true;
  };

  const createPM = async (data) => {
    // console.log("Create PM", data);
    try {
      const billing_details = {
        name: profile.firstname + " " + profile.lastname,
        email: profile.email,
        phone: profile.phone,
        address: {
          city: data.city,
          state: data.state,
          country: "US",
          line1: data.address1,
          // addressLine2: "Texas",
          postal_code: data.zipcode,
        },
      }; //

      if (!checkCardIsValid()) {
        setIsSaving(false);
        return;
      }

      const result = await stripe.createPaymentMethod({
        type: "card",
        card,
        billing_details,
      });

      if (result.error) {
        console.log(result.error);
        _showAlert(
          "error",
          "Card verification failed, check your billing details or try another card."
        );
        return;
      }

      const pid = result.paymentMethod.id;
      await dispatch(planActions.attachPM(pid));
      setIsSaving(false);
      setIsOpen(false);
      setRefresh(true);
      reset();
      _showAlert("success", "Card added successfully.");
    } catch (err) {
      console.log("I am here");
      _showAlert("error", err.message);
    }
  };

  return (
    <VStack space={2} style={{ zoom: "90%" }}>
      <VStack space={4}>
        <Text style={{ fontFamily: "Poppins_medium", fontSize: vw(13) }}>
          Card Number
        </Text>
        <VStack
          style={{
            width: "100%",
            borderColor: "rgba(0,0,0,0.15)",
            borderWidth: vw(1.5),
            padding: vw(9),
            borderRadius: 4,
            marginTop: vh(-15),
            justifyContent: "center",
          }}
        >
          <CardNumberElement
            onChange={onChange}
            options={{
              style: {
                base: {
                  // textAlign:"center"
                },
              },
            }}
          />
        </VStack>
      </VStack>
      <HStack marginBottom={vh(20)} justifyContent={"space-between"}>
        <VStack>
          <Text style={{ fontFamily: "Poppins_medium", fontSize: vw(13) }}>
            Card Expiry
          </Text>
          <VStack
            style={{
              width: vw(100),
              borderColor: "rgba(0,0,0,0.15)",
              borderWidth: vw(1.5),
              padding: vw(9),
              borderRadius: 4,
            }}
          >
            <CardExpiryElement
              options={{
                style: {
                  // base: inputStyle,
                },
              }}
            />
          </VStack>
        </VStack>
        <VStack>
          <Text style={{ fontFamily: "Poppins_medium", fontSize: vw(13) }}>
            Card CVC
          </Text>
          <VStack
            style={{
              width: vw(100),
              borderColor: "rgba(0,0,0,0.15)",
              borderWidth: vw(1.5),
              padding: vw(9),
              borderRadius: 4,
            }}
          >
            <CardCvcElement
              options={{
                style: {
                  // base: inputStyle,
                },
              }}
            />
          </VStack>
        </VStack>
      </HStack>
      <HStack justifyContent={"flex-end"} space={4}>
        <ShadowButton
          space={2}
          label='Cancel'
          variant='outline'
          onPress={() => {
            // reset();
            setIsOpen(false);
          }}
          style={{ height: vh(36) }}
        />
        <ShadowButton
          space={3}
          isLoading={isSaving}
          label='Save'
          onPress={handleSubmit(onSubmit)}
          style={{ height: vh(36) }}
        />
      </HStack>
    </VStack>
  );
};

const AddressForm = ({ errors, control }) => {
  return (
    <VStack
      style={{ zoom: "90%" }}
      // marginTop={vh(-30)}
      marginBottom={vh(20)}
      width={"100%"}
      space={6}
    >
      <Text style={{ fontFamily: "Poppins_medium", fontSize: vw(13) }}>
        Billing Details
      </Text>
      <ShadowInput
        isRequired
        name='address1'
        label={"Address1 "}
        placeholder='Enter address 1'
        control={control}
        rules={{
          required: true,
          message: "Please enter address 1",
        }}
        style={{ marginTop: vh(-20), width: "100%" }}
        invalid={errors.address1 ? true : false}
        error={errors.address1 ? errors.address1.message : ""}
        inputStyle={{}}
      />
      <ShadowInput
        name='address2'
        label={"Address2 "}
        placeholder='Enter address2'
        control={control}
        rules={
          {
            // required: true,
            // message: "Please enter address2",
          }
        }
        invalid={errors.address2 ? true : false}
        error={errors.address2 ? errors.address2.message : ""}
        style={{ width: "100%" }}
        inputStyle={{}}
      />
      <HStack
        space={4}
        style={{ width: "50%" }}
        justifyContent={"space-between"}
      >
        <ShadowInput
          isRequired
          name='city'
          label={"City "}
          placeholder='City'
          control={control}
          rules={{
            required: true,
            message: "Please enter city",
          }}
          invalid={errors.city ? true : false}
          error={errors.city ? errors.city.message : ""}
          inputStyle={{}}
        />
        <HStack
          space={4}
          style={{ width: "42%" }}
          justifyContent={"space-between"}
        >
          <ShadowInput
            isRequired
            name='state'
            label={"State "}
            placeholder='State'
            control={control}
            rules={{
              required: true,
              message: "Please enter state",
            }}
            invalid={errors.state ? true : false}
            error={errors.state ? errors.state.message : ""}
            inputStyle={{}}
          />
          <ShadowInput
            isRequired
            name='zipcode'
            label={"Zipcode "}
            placeholder='Zipcode'
            maxLength={5}
            control={control}
            rules={{
              // valueAsNumber: true,
              pattern: {
                value: /^[0-9]{5}?$/,
                message: "Invalid zipcode",
              },
              required: {
                value: true,
                message: "Please enter zipcode",
              },
              maxLength: {
                value: 5,
                message: "Invalid zipcode",
              },
              minLength: {
                value: 5,
                message: "Invalid zipcode",
              },
            }}
            invalid={errors.zipcode ? true : false}
            error={errors.zipcode ? errors.zipcode.message : ""}
            inputStyle={{}}
          />
        </HStack>
      </HStack>
    </VStack>
  );
};

const AddCard = ({ setRefresh, profile, _showAlert, dispatch, landscape }) => {
  const [isOpen, setIsOpen] = useState(false);

  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    setValue,
    getValues,
    trigger,
  } = useForm({
    mode: "all",
  });

  return (
    <>
      <Pressable
        onPress={() => {
          setIsOpen(true);
        }}
      >
        <Center
          style={{
            backgroundColor: "rgba(0,0,0,0.10)",
            width: vw(100),
            height: vh(100),
            borderRadius: 10,
            margin: vh(10),
          }}
        >
          <Center
            style={{
              width: vw(32),
              height: vh(32),
              backgroundColor: "#919191",
              borderRadius: 50,
            }}
          >
            <Text
              style={{
                color: "#E5E5E5",
                fontSize: vw(24),
                marginTop: vh(-4),
              }}
            >
              +
            </Text>
          </Center>
        </Center>
      </Pressable>
      <ShadowModal
        placement={"top"}
        size={"lg"}
        close={() => {
          reset();
          setIsOpen(false);
        }}
        // height={vh(700)}
        // bodyHeight={vh(profile && !profile.address ? 400 : 200)}
        isOpen={isOpen}
        header={"Add Card"}
      >
        <VStack style={{ marginTop: vh(-15) }} space={4}>
          {profile && !profile.address && (
            <AddressForm
              setValue={setValue}
              trigger={trigger}
              getValues={getValues}
              errors={errors}
              control={control}
            />
          )}
          <StripeWrapper>
            <CardForm
              _showAlert={_showAlert}
              profile={profile}
              setIsOpen={setIsOpen}
              dispatch={dispatch}
              handleSubmit={handleSubmit}
              setRefresh={setRefresh}
              reset={reset}
            />
          </StripeWrapper>
        </VStack>
      </ShadowModal>
    </>
  );
};

const PaymentMethods = ({ _showAlert, profile, landscape }) => {
  const dispatch = useDispatch();
  const [cards, setCards] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [refresh, setRefresh] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const {
    control,
    formState: { errors },
    reset,
  } = useForm({
    mode: "all",
    defaultValues: {},
  });

  const getCards = useCallback(async () => {
    try {
      const userCards = await dispatch(planActions.fetchCards());
      setCards(userCards);
      setRefresh(false);
      // setAddCard(userCards && userCards.length === 0);
    } catch (err) {
      setRefresh(false);
      _showAlert("error", err.message);
    }
  }, [dispatch, _showAlert, setRefresh]);

  useEffect(() => {
    let unmounted = false;
    // console.log("Refresh", refresh)

    if (!unmounted && true) {
      getCards();
    }

    return () => {
      unmounted = true;
    };
  }, [refresh, getCards]);

  const PaymentItem = ({ data, index }) => {
    const { brand, last4 } = data.card;
    const [isOpenDelete, setIsOpenDelete] = useState(false);

    const delPM = async (id) => {
      try {
        const pm = await dispatch(planActions.delPM(id));
        if (pm === id) {
          _showAlert("success", "Payment method removed successfully.");
          setRefresh(true);
        }
      } catch (err) {
        console.log(err);
        _showAlert(
          "error",
          "something went wrong, cannot remove payment method."
        );
      }
    };

    const setAsDefaultPM = async (id) => {
      try {
        await dispatch(planActions.setAsDefaultPM(id));
        _showAlert("success", "Payment method set as default");
        setRefresh(true);
      } catch (err) {
        console.log(err);
        _showAlert(
          "error",
          "something went wrong, cannot set payment method as default."
        );
      }
    };

    return (
      <>
        <Pressable
          onPress={() => {
            if (!data.default) {
              setAsDefaultPM(data.id);
            }
          }}
          style={{
            borderRadius: 10,
            height: vh(100),
            width: vw(200),
            marginVertical: vh(10),
            marginHorizontal: vw(10),
            paddingTop: vh(5),
            borderColor: Colors.primary,
            borderWidth: 1.5,
            paddingHorizontal: vw(10),
          }}
        >
          {data.default && (
            <Center
              rounded={"full"}
              style={{
                position: "absolute",
                top: vh(-6),
                right: vw(-6),
                backgroundColor: "white",
              }}
            >
              <CheckCircleIcon
                style={{
                  color: Colors.primary,
                }}
              />
            </Center>
          )}
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(13),
              color: "#303030",
              marginTop: vh(10),
            }}
          >
            {"Card " + (index + 1)}
          </Text>
          <HStack style={{ marginTop: vh(10) }}>
            <Center
              style={{
                width: vw(30),
                height: vh(18),
                borderWidth: 1,
                borderColor: "rgba(0,0,0,0.15)",
              }}
            >
              {brand === "visa" && (
                <VisaCardIcon width={vw(30)} height={vh(18)} />
              )}
              {brand === "mastercard" && (
                <MasterCardICon width={vw(30)} height={vh(18)} />
              )}
            </Center>
            <Text
              style={{
                fontFamily: "Poppins",
                fontSize: vw(13),
                color: "#303030",
                marginLeft: vw(10),
                paddingTop: vh(3),
              }}
            >
              {"**** **** **** "}
            </Text>
            <Text
              style={{
                fontFamily: "Poppins",
                fontSize: vw(11),
                color: "#303030",
                marginTop: vh(1),
              }}
            >
              {last4}
            </Text>
          </HStack>
          <Pressable
            onPress={async (e) => {
              e.preventDefault();
              setIsOpenDelete(true);
            }}
            style={{
              position: "absolute",
              marginTop: vh(6),
              bottom: vw(6),
              right: vw(6),
            }}
          >
            <CardRemoveIcon />
          </Pressable>
        </Pressable>
        <ShadowModal
          style={{ top: vh(-200) }}
          size={"md"}
          isOpen={isOpenDelete}
          close={() => {
            setIsOpenDelete(false);
          }}
          header='Delete Payment Method'
          buttons={
            <HStack space={2}>
              <ShadowButton
                space={3}
                onPress={() => {
                  setIsOpenDelete(false);
                }}
                color='rgba(0,0,0, 0.5)'
                variant='outline'
                label='No'
                style={{
                  height: vh(36),
                }}
              />
              <ShadowButton
                space={3}
                onPress={async () => {
                  await delPM(data.id);
                }}
                color='white'
                label='Yes'
                style={{
                  height: vh(36),
                }}
              />
            </HStack>
          }
        >
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(15),
              color: "rgba(0,0,0,0.8)",
            }}
          >
            Are you sure you want to delete this payment method?
          </Text>
        </ShadowModal>
      </>
    );
  };

  const renderItem = useCallback(({ item, index }) => {
    return <PaymentItem index={index} data={item} />;
  }, []);

  return (
    <>
      <FlatList
        style={{ marginLeft: vw(-10) }}
        ListEmptyComponent={
          <Center>
            <Text>No payment methods found.</Text>
          </Center>
        }
        scrollEnabled={cards.length > 0}
        ListHeaderComponent={
          <AddCard
            dispatch={dispatch}
            profile={profile}
            _showAlert={_showAlert}
            setRefresh={setRefresh}
            landscape={landscape}
          />
        }
        // style={{ marginHorizontal: 12 }}
        horizontal={true}
        data={cards}
        renderItem={renderItem}
        keyExtractor={(item, index) => item.id}
      />
      <ShadowModal
        size={"md"}
        header='Email'
        isOpen={isOpen}
        close={() => {
          reset();
          setIsOpen(false);
        }}
        buttons={
          <HStack space={2}>
            <ShadowButton
              color={"rgba(0,0,0, 0.5 )"}
              label='Cancel'
              variant='outline'
              onPress={() => {
                reset();
                setIsOpen(false);
              }}
            />
            <ShadowButton
              isLoading={isLoading}
              label='Save'
              onPress={() => {}}
            />
          </HStack>
        }
      >
        <HStack
          style={{
            width: vw(300),
            justifyContent: "space-between",
            zIndex: 9999,
          }}
          space={4}
        >
          <ShadowInput
            isRequired
            name='label'
            label={"Label "}
            placeholder='Enter email label'
            control={control}
            rules={{
              required: true,
              message: "Please enter email label",
            }}
            invalid={errors.label ? true : false}
            error={errors.label ? errors.label.message : ""}
            inputStyle={{}}
          />
          <ShadowInput
            isRequired
            name='email'
            label={"Email "}
            placeholder='Enter email'
            control={control}
            rules={{
              required: true,
              message: "Please enter valid email address",
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: "invalid email address",
              },
            }}
            invalid={errors.email ? true : false}
            error={errors.email ? errors.email.message : ""}
            inputStyle={{}}
          />
        </HStack>
      </ShadowModal>
    </>
  );
};

export default React.memo(PaymentMethods);
